 *{
  margin: 0;
  padding: 0;
 }
.form{
  margin-top: 2%;
}
.footer{
  background-color: rgb(70, 139, 218);
  position: fixed;
  bottom: 0;
  margin: auto;
  width: 100%;
}
.footer ul{
  gap: 5%;
  list-style-type: none;
  margin-left: 3%;
}
.footer ul li a{
  color: white;
  font-size: 15px;
  font-family: cursive;
}
.footer ul li a:hover{
  color: rgb(70, 139, 218);
}
.menu ul{
  display: flex;
  list-style: none;
  /* justify-content: space-evenly; */
  gap: 10%;
  margin-right: 10%;
}
.menu ul li{
  color: rgb(70, 139, 218);
  font-family: cursive;
  font-size: 18px;
}
.menu{
  position: sticky;
  margin: 0;
  top: 0;
}